<template>
  <div>
    <br />
    <h1>{{ userNickname }} </h1>
    <div v-show="isLogin">
      <router-link to="/profile/update">Profile Update</router-link>
      <br />
      <div class="card">
        <div class="card-body">
          <h5 class="card-title">UserProfile</h5>
          <div class="row">
            <div class="col-sm-4">
              <div class="card">
                <div class="card-body">
                  <div>
                    <div>uri : {{ userImgUri }}</div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-sm-8">
              <div class="card">
                <div class="card-body">
                  <div class="row">
                    <div class="col-sm-6"> : {{ userNo }}</div>
                    <div class="col-sm-6"> : {{ userNickname }}</div>
                    <br />
                    <div class="col-sm-6"> : {{ userGrade }}</div>
                    <div class="col-sm-6"> : {{ isLogin }}</div>
                    <br />
                    <div class="col-sm-6"> : {{ userDesc }}</div>
                    <div class="col-sm-6"> : {{ userComment }}</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <br />
      <div class="card">
        <div class="card-body">
          <h5 class="card-title">Sell Items</h5>
          <div class="row">
            <div class="col-sm-4" v-for="index in sellItem" :key="index">
              <div>
                <div class="card">
                  <div class="card-body">
                    <div>
                      <img
                        v-bind:src="
                          'data:image/png;base64,' + index.itemMainImg
                        "
                      />

                      <br />
                      {{ index.item.itemName }}
                      <br />
                      {{ index.item.modelNo }}
                      <br />
                      {{ index.item.startPrice }}
                      <br />
                      {{ index.item.regTime }}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "@/utils/axios.js";

export default {
  name: "SsauctionProfile",

  data() {
    return {
      userNickname: "",
      userNo: 0,
      userGrade: 0,
      userDesc: "",
      userComment: "",
      userImgUri: "",
      sellItem: [],
      buyItem: [],
    };
  },

  mounted() {
    this.userNickname = this.$store.state.user.nickname;
    this.userNo = this.$store.state.user.userNo;
    this.userGrade = this.$store.state.user.grade;
    this.isLogin = this.$store.state.user.isLogin ? true : false;
    this.getUserInfo();
    this.getItemInfo();
  },

  methods: {
    getUserInfo: async function () {
      console.log(this.userNickname);
      console.log(this.userNo);
      console.log(this.isLogin);
      await axios
        .get("/users/profile/" + this.userNo)
        .then(({ data }) => {
          this.userDesc = data.userDesc;
          this.userComment = data.userComment;
          this.userImgUri = data.userImgUri;
        })
        .catch(({ data }) => {
          console.log(data);
        });
    },
    getItemInfo: async function () {
      console.log(this.userNickname);
      console.log(this.userNo);
      console.log(this.isLogin);
      await axios
        .get("/houses/profile/" + this.userNo)
        .then(({ data }) => {
          console.log(data);
          console.log(data[0]);
          if (data === null) {
            console.log("null data");
          } else {
            this.sellItem = data.sellItemsList;
            console.log(this.sellItem[0].itemMainImg);
            console.log(this.sellItem);
            console.log(this.buyItem);
          }
        })
        .catch(({ data }) => {
          console.log(data);
        });
    },
  },
};
</script>

<style></style>
