// ];

// const disabledDate = (time: Date) => {
//   return time.getTime() > Date.now();
// };

export default {
  name: "SsauctionHouse",
  components: { ItemImagePreview },
  data() {
    return {
      item: {
        itemName: null,
        itemModelNo: null,
        itemQuality: "S",
        itemStartPrice: null,
        itemDesc: null,
        itemSellerNo: null,
        itemDealStatus : "SELL",
        userNo: this.$store.state.user.userNo,
        itemDealStatus: "SELL",
        userNo: 1, //        1 
      },
      ctgr: {
        itemNo: 1,
        ctgrName: [],
      },
      house: {
        houseTitle: null,
        // Datepicker     
        // houseAucTime, houseStatus     .
        houseAucTime: "2022-02-22T22:22:22",
        houseStatus: 0,
      },
      houseDate: new Date(),
      modelConfig: {
        type: "string",
        mask: "YYYY-MM-DDThh:mm:ss",
      },
      itemImages: [],
    };
  },

  mounted() {},

  methods: {
    imageUpload() {
      // $refs  DOM  input files   FileList  .
      // FileList  read only  .
      //    this.itemImages  files  File   .
      Array.prototype.push.apply(this.itemImages, this.$refs.itemImage.files);
      console.log(this.itemImages);
      this.$refs.itemImage.value = "";
    },
    createHouse() {
      // this.house.houseAucTime = this.houseAucTime;
      console.log(this.house);

      console.log(this.item);
      console.log(this.ctgr);

      // file multipart/form-data   .
      // multipart/form-data   JSON  body     .
      //  json Blob     .
      const housejson = JSON.stringify(this.house);
      const itemjson = JSON.stringify(this.item);

      const ctgrjson = JSON.stringify(this.ctgr);
      const houseblob = new Blob([housejson], { type: "application/json" });
      const itemblob = new Blob([itemjson], { type: "application/json" });
      const ctgrblob = new Blob([ctgrjson], { type: "application/json" });

      // html form   submit formData  multipart/form-data  .
      // form          formData  .
      let formData = new FormData();
      formData.append("itemDto", itemblob);
      formData.append("houseDto", houseblob);
      formData.append("ctgrDto", ctgrblob);

      // Spring  file     Json    .
      //      file .
      for (let img of this.itemImages) {
        formData.append("files", img);
      }

      axios
        .post("/houses", formData, {
          headers: { "Content-Type": "multipart/form-data" },
        })
        .then((data) => {
          console.log(data);
          // this.sendFile();
          this.itemImages.splice(0);
          alert(" ");
          this.$route.push({ name: "Home" });
        })
        .catch((error) => {
          alert(error);
        });

      console.log(ctgrjson);

      axios.post("/categories", ctgrjson).then((data) => {
        console.log(data);
      });
      // this.sendFile();
      this.itemImages.splice(0);
    },

    deleteImg(index) {
      this.itemImages.splice(index, 1);
      console.log(this.$refs.itemImage);
    },

    deleteImg(index) {
      this.itemImages.splice(index, 1);
      console.log(this.$refs.itemImage);
    },
  },
