<template>
  <div>
    <h1>{{ timerStr }}</h1>
    <button @click="timerStart"></button>
    <button @click="timerReset"></button>
    <button @click="timerPause"></button>
    <button @click="timerStop"></button>
  </div>
</template>

<script>
export default {
  name: "SessionTimer",
  data() {
    return {
      timer: null,
      startTime: 5,
      timeCounter: null,
      timerStr: null,
    };
  },
  mounted() {
    this.timerStop(this.timer);
    this.timerStr = this.formatTime(this.startTime);
    this.timer = null;
  },
  methods: {
    timerStart() {
      if (this.timer !== null) {
        console.log("  .");
        return;
      }
      console.log("start of timerStart()");
      this.timeCounter = this.startTime;

      this.timer = setInterval(() => {
        this.timeCounter--;
        this.timerStr = this.formatTime();

        if (this.timeCounter <= 0) {
          console.log(" .");
          this.timerPause(this.timer);
        }
      }, 1000);
    },
    timerPause() {
      clearInterval(this.timer);
      this.timer = null;
    },
    timerStop() {
      if (this.timer !== null) {
        clearInterval(this.timer);
        this.timeCounter = 0;
      }
      this.timer = null;
    },
    timerReset() {
      this.timeCounter = this.startTime;
    },
    formatTime(timeInSeconds) {
      let time = timeInSeconds / 60;
      let minutes = parseInt(time);
      let seconds = parseInt(timeInSeconds % 60);
      return (
        minutes.toString().padStart(2, "0") +
        ":" +
        seconds.toString().padStart(2, "0")
      );
    },
  },
};
</script>

