<template>
<div style="margin-top: 100px">
  <h1> </h1>
  <p>
       ()   . 
  </p>

  <div class="box">
    <div>
    <input id="user_phoneNo" type="string" v-model="phoneNo" placeholder=" (- )">
    </div>
    <br>
    <br>
    <!-- <h1>{{ phoneNo }}</h1> -->
    <button class="btn_pink" @click="findId"> </button>

    <div v-if="modalSmsCode == true">
      <br/>
      <br/>
        <div>
          <input id="user_smsCode" type="string" v-model="smsCode" placeholder=" ">
        </div>
      <br>
        <button class="btn_pink" @click="verifyCode"> </button>
    </div>
  </div>
  </div>
</template>

<script>
  import axios from "@/utils/axios";

  export default {
  name: "SsauctionFindId",

  data() {
    return {
      phoneNo: "",
      userEmail: "",
      returnValue: {},
      modalSmsCode : false,
      randCode: null,
      smsCode: ""
    };
  },

  mounted() {},

  methods: {
    findId: function () {
      // console.log("FindId start");
      // console.log(this.phoneNo);
      // console.log("start getting");
      axios.get("/users/findId/" + this.phoneNo).then(({ data }) => {
        // console.log(data.userPhoneNo);
        // console.log(data.userEmail);
        this.userEmail = data.userEmail;
        this.returnValue = data;
        // console.log(this.returnValue);
        if (this.returnValue !== "") {
          //    + axios.post  sms 
          this.modalSmsCode = true;
          axios.post("/users/sendSMS/" + this.phoneNo).then((smsData) => {
            // console.log(smsData);
            this.randCode = smsData.data;
            // console.log(`randCode: ${this.randCode}`);
            });
          } else {
          alert("    .");
        }
      });
    },
    verifyCode: function() {
      // console.log(parseInt(this.smsCode));
      // console.log(parseInt(this.randCode));
      // if ( === ) {    }
      if (parseInt(this.smsCode) === parseInt(this.randCode)) {
        alert("( )  .\n" + this.userEmail);
        this.$router.push('/login')
      } else {
        alert("  .");
      }
    },
  },
};
</script>

